.vertical-navbar {
    /* Fixed position vertical navbar for quick navigation üìå */
    position: fixed;
    padding: var(--padding-small); /* Using a CSS variable for padding üìè */
    top: 78px; /* Navbar offset from the top üîù */
    right: 0; /* Positioned to the right of the screen üñ±Ô∏è */
    width: 200px; /* Set width for the navbar üìê */
    background-color: #56aced; /* Eye-catching background color üé® */
    justify-content: center; /* Center-align the content ü§π */
    color: white; /* Text color for readability üåü */
    padding: 10px; /* Additional padding for inner spacing üõãÔ∏è */
    border-radius: 10px; /* Smooth edges for a modern look ‚ú® */
    box-shadow: 2px 2px 5px rgba(255, 255, 255, 0.619); /* Subtle shadow effect for depth üñºÔ∏è */
    z-index: 1000; /* Ensure the navbar appears above other elements üöÄ */
    animation: slideIn 0.3s ease-in-out; /* Entry animation for a dynamic feel üé• */
  }
  
  .vertical-navbar ul {
